<template>
  <div class="game_detail">
    <div class="game_detail_wrap">
      <div class="recommend">
        <div class="avatar_wrap">
          <img src="../../assets/game/touxiang01.png">
          <img src="../../assets/game/touxiang02.png">
          <img src="../../assets/game/touxiang03.png">
        </div>
        <div style="margin-left: 0.38rem">
          <label class="rec_name"> 彩虹是我家 </label>
          <label class="rec_msg">等20万位小伙伴推荐了该游戏</label>
        </div>      
      </div>

      <div class="des three_line_text">《阴阳师》是由中国网易移动游戏公司自主研发的3D日式和风回合制RPG手游。2016年6月1日11：00，《阴阳师》开放安卓首测 [1]  ；同年9月2日... ...
      </div>

      <div class="author bottom-line" @click="goToDeveloper()">
        <div class="left">
          <label style="color: #8888EB;">网易游戏</label>
          <label style="margin-top: 0.12rem;">开发商</label>
        </div>
        <div class="right">
          <img src="../../assets/jinru_little.png">
        </div>
      </div>
      <div class="new_fauture game-detail-section bottom-line">
        <div class="new_fauture_row">
          <label style="font-size: 0.30rem;color: #000000;">新功能</label>
          <label style="font-size: 0.30rem;color: #8888EB;">网易游戏</label>
        </div>
        <div class="new_fauture_row">
          <label>版本 1.1.7</label>
          <label>9小时前</label>
        </div>
        <div class="new_fauture_row">
          <div class="content_style">更新内容说明:1.本次更新2.是的范德萨</div>
          <div class="more">更多</div>
        </div>
      </div>
      <div class="info game_detail_section bottom-line">
        <label style="font-size: 0.30rem; color: #000000; font-weight:400;">信息</label>
        <div class="info-row">
          <label>开发商</label>
          <label>网易游戏</label>
        </div>
        <div class="info-row">
          <label>大小</label>
          <label>1.7G</label>
        </div>
        <div class="info-row">
          <label>类别</label>
          <label>游戏： 动作游戏</label>
        </div>
        <div class="info-row">
          <label>兼容性</label>
          <label>可在此手机上使用</label>
        </div>
        <div class="info-row">
          <label>语言</label>
          <label>简体中文</label>
        </div>
        <div class="info-row">
          <label>年龄分级</label>
          <label>12+</label>
        </div>
        <div class="info-row">
          <label>App内购买项目</label>
          <label>是</label>
        </div>
        <div class="info-row">
          <label>版权</label>
          <label>**********</label>
        </div>
      </div>
      <div style="padding-top: 0.22rem;padding-bottom: 0.30rem">相关游戏</div>
    </div>

<!--     这样写有BUG,直接在网址写入路径，会出现错误,不会横向滚动，用路由跳转则没有错误
    <div style="width: 300px;height: 80px;">
      <cube-scroll ref="scroll" :data="items2" direction="horizontal" :options="scrollOptions">
        <div class="list-wrapper">
          <div v-for="item in items2" class="list-item-two">{{ item }}</div>
        </div>
      </cube-scroll> 
    </div> -->
    <!-- 所以换成这样写 -->
    <div class="relation_game">
      <div style="position: absolute;top: 0;left: 0;width: 100%;height: 100%;">
        <cube-scroll ref="cube" :data="items2" :options="scrollOptions" >
          <div class="relation_game_wrap">
            <div v-for="item in items2" class="relation_game_item" @click="goToGame()">
              <div style="display: flex;align-items: flex-end;">
                <img class="avatar" src="../../assets/game/tubiao.png">
                <img class="icon" src="../../assets/android.png">
                <img class="icon" src="../../assets/ios.png">
              </div>
              <div style="margin-top: 0.15rem"><label>皇室战争</label><label style="margin-left: 0.12rem">3.8分</label></div>
              <div style="margin-top: 0.15rem;font-size: 0.20rem">30万人推荐</div>
            </div>
          </div>
        </cube-scroll>        
      </div>
    </div>
  </div>
</template>

<script >

  const _data2 = [
    '111',
    '2222 ',
    '333333️ ',
    '44444444 ',
    '5555555555 ',
    '666666666666 ',
    '77777777777777 ',
    '🙈 8888888888888 ',
    '999999999999999999 ',
  ]

  export default {
    props: {
      data: {
        type: Array,
        default: []
      }
    },
    data() {
      return {
        items2: _data2,
        scrollOptions: {
          stopPropagation: true,
          eventPassthrough: 'horizontal'
        },
        recommends: ['touxiang01', 'touxiang02', 'touxiang03']
      }
    },
    methods: {
      goToDeveloper() {
        this.$emit("goToDeveloper")
      },
      goToGame() {
        this.$emit("goToGame")
      },
      resolveTitle (item) {
        return `${item.name}关注了问题 · ${item.postTime} 小时前`
      },
      resolveQuestionFollowers (item) {
        return `${item.answers} 赞同 · ${item.followers} 评论`
      }
    },
    components: {
    }
  }
</script>


<style scoped lang="stylus">
  
  @import "~common/stylus/variable"
  
  .game_detail_wrap
    box-sizing: border-box
    margin-left: 0.40rem
    margin-right: 0.36rem
    color: $content-color
    font-size: 0.25rem
    .recommend
      display: flex
      align-items: center
      margin-top: 0.3rem
      .avatar_wrap
        img
          width: 0.72rem
          height: 0.72rem
        for n in 2 3
          img:nth-child({n})
            margin-left: -0.36rem
      .rec_name
        color: #8888EB
    .des
      margin-top: 0.33rem
    .author
      display: flex
      justify-content: space-between
      align-items: center
      margin-top: 0.28rem
      padding-bottom: 0.25rem
      .left
        display: flex
        flex-direction: column
      .right
        width: 0.195rem
        height: 0.338rem
    .new_fauture
      position: relative
      .new_fauture_row
        display: flex
        justify-content: space-between
        padding-top: 0.17rem
        &:last-child
          height: 0.6rem
      .more
        position: absolute
        bottom: 0.19rem
        right: 0.1rem
        color: #FFA535
    .info
      padding-top: 0.17rem
      .info-row
        display: flex
        justify-content: space-between
        font-size: 0.25rem
        padding-top: 0.38rem
        label:nth-child(2)
          color: #000000
        &:nth-of-type(1)
          padding-top: 0.24rem
  .relation_game
    position: relative
    width: calc(100% - 0.40rem)
    height: 2.13rem
    margin-left: 0.40rem
    padding-bottom: 0.73rem
    -webkit-overflow-scrolling: touch
    .relation_game_wrap
      width: 100%
      overflow-x: scroll
      overflow-y: hidden
      white-space: nowrap
      .relation_game_item
        width: 2.77rem
        margin-right: 0.19rem
        display: inline-block
        font-size: 0.25rem
        color: $content-color
        border: 1px solid $spec-color
        border-radius: 5px;
        padding-left: 0.16rem
        padding-top: 0.15rem
        padding-bottom: 0.19rem
        .avatar
          width: 0.97rem
          height: 0.97rem
        .icon
          margin-left: 0.12rem
          width: 0.29rem
          height: 0.36rem
  .game_detail_section
    display: flex
    flex-direction: column
    padding-bottom: 0.56rem
  .list-wrapper
    padding: 0 10px
    line-height: 60px
    white-space: nowrap
  .cube-scroll-content
    display: inline-block
  .list-item-two
    margin-right: 15px;
    background-color: red
    display: inline-block

      
    
  
</style>


