<template>
  <div class="game">
    <div class="video">
      <img src="../../assets/game/timg-119.png">
    </div>
    <div class="game_name_subs">
      <div class="left">
        <img class="avatar" src="../../assets/game/tubiao.png">
        <div class="des">
          <div class="app_info">
            <label>阴阳师</label>
            <div class="ver_info">
              <label class="language">中文</label>
              <img class="icon" src="../../assets/android.png">
              <img class="icon" src="../../assets/ios.png">
            </div>
          </div>
          <div class="categ">
            <label>卡牌</label>
            <label>和风</label>
            <label>策略</label>
          </div>
        </div>
      </div>
      <div class="right">
        <label class="score">5.0分</label>
        <label class="get_way">免费获取</label>
      </div>
    </div>
    
    <div class="game_tab_composite">
      <tab-composite :data="mydata" :tabLabels="tabLabels" defaultLabel= "详情">
        
        <game-detail :data="mydata" slot="left" @goToDeveloper="goToDeveloper" @goToGame="goToGame"></game-detail> 

        <div slot="center" style="height: 100%;">
          <cube-scroll :data="mydata" :options="evalationScrollOptions" @pulling-down="evaluationPullingDown()" @pulling-up="evaluationPullingUp()">
            <game-evaluation :data="mydata"></game-evaluation>
          </cube-scroll>
        </div>
        
        <div class="game_event" slot="right">
          <cube-scroll :data="mydata" :options="eventScrollOptions" @pulling-down="eventPullingDown()" @pulling-up="eventPullingUp()">
            <game-event :data="mydata" @goToEventDetail="goToEventDetail"></game-event>
          </cube-scroll>
        </div>

      </tab-composite>      
    </div>

    
  </div>
</template>

<script type="text/javascript">
  import TabComposite from 'pages/tab-composite.vue'
  import GameDetail from './game-detail.vue'
  import GameEvaluation from 'pages/evaluation.vue'
  import GameEvent from 'pages/event.vue'
  import SendMessageToApp from '../../jsAppInteractive'
  import { scrollOptions } from 'common/js/util.js'

  export default {
    data () {
      return {
        tabLabels: [{
          label: '详情'
        }, {
          label: '评价'
        }, {
          label: '动态'
        }],
        mydata: ['1','2','3'],
        evalationScrollOptions: scrollOptions(false, false),
        eventScrollOptions: scrollOptions(),
      }
    },
    mounted() {
      // 数据大于20开启上拉刷新
      // this.eventScrollOptions.pullUpLoad = true
    },
    methods: {
      goToGame() {
        SendMessageToApp('goToGame')
      },
      goToDeveloper() {
        SendMessageToApp('goToDeveloper')
        // this.$router.push({
        //   name: 'Developer'
        // })
      },
      goToEventDetail() {
        // console.log("实现了goToEventDetail")
        SendMessageToApp('goToEventDetail')
        // this.$router.push({
        //   name: 'EventDetail'
        // })        
      },
      evaluationPullingUp() {
        setTimeout( () => {
          this.mydata = this.mydata.concat(['1','2'])
        }, 1500)
      },
      eventPullingDown() {
        setTimeout( () => {
          this.mydata = ['1', '2', '3']
        }, 1500)
      },
      eventPullingUp() {
        setTimeout( () => {
          this.mydata = this.mydata.concat(['1','2'])
        }, 1500)
      }
    },
    components: {
      TabComposite,
      GameDetail,
      GameEvaluation,
      GameEvent
    }
  }
</script>


<style scoped lang="stylus">
  .game
    background-color: #FFF
    .video
      width: 100%
      height: 3.91rem
    .game_name_subs
      padding: 0.25rem 0.3rem
      display: flex
      justify-content: space-between
      .left
        min-width: 60%
        display: flex
        justify-content: flex-start
        .avatar
          width: 0.97rem
          height: 0.97rem
        .des
          box-sizing: border-box
          margin-left: 0.24rem
          display: flex
          flex-direction: column
          color: #8E8E93
          .app_info
            display: flex
            justify-content: space-between
            font-size: 0.3rem
            .ver_info
              display: flex
              align-items: center
              font-size: 0.2rem
              padding-left: 0.39rem
              .language
                margin-right: 0.13rem
              .icon
                width: 0.29rem
                height: 0.36rem
              :last-child
                margin-left: 0.07rem
          .categ
            margin-top: 0.24rem
            font-size: 0.25rem
      .right
        display: flex
        flex-direction: column
        justify-content: space-between
        .score
          color: #8E8E93
          font-size: 0.25rem
          text-align: right
        .get_way
          margin-top: 0.19rem
          width: 1.49rem
          height: 0.5rem
          background: rgba(255,165,53,1)
          color:rgba(255,255,255,1)
          font-size: 0.2rem
          text-align: center
          line-height: 0.5rem
    .game_tab_composite
      position: absolute
      z-index: 10
      top: (3.91rem + 0.5rem + 0.97rem)
      bottom: 0
      left: 0
      width: 100%
      .game_event
        height: 100%
        box-sizing: border-box
        margin-left: 0.40rem
        margin-right: 0.36rem
        color: $content-color
        font-size: 0.25rem
      
      
    
  
</style>


